import moment from 'moment';
import React, { FC, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Alert, Col, CustomInput, Row } from 'reactstrap';
import {
  currentDate,
  DATES,
  formatDate,
  InputGroupInline,
  InputType,
  parseLastDay,
  parseRefToDate,
  WindowComponent,
  WindowComponentProps,
} from 'summer';
import { hasEmpresa } from '../../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../../dependencies/empresaMFPDependency';
import { hasGrupoEmpresaConfig } from '../../../../../dependencies/grupoEmpresaConfigDependency';
import { getDefaultReportBasicValues } from '../../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../models/dtos/ger/reportViewOptions';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import CustomizacaoTermoIS from '../../../../components/mfp/customizacaoTermoIS/CustomizacaoTermoIS';
import EventoIS from '../../../../components/mfp/eventoIS/EventoIS';
import VinculoIS from '../../../../components/mfp/vinculoIS/VinculoIS';
import ReportForm from '../../../../components/report/ReportForm';
import { AcordoProrrogacaoHoraExtraRV, CLASS_KEY, REPORT_EVENT, REPORT_TITLE } from './acordoProrrogacaoHoraExtraRV';

const DEFAULT_VALUES: AcordoProrrogacaoHoraExtraRV = {
  ...getDefaultReportBasicValues(REPORT_TITLE),
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT, DATES.value),
  exibirConfiguracaoDadosEmpresa: false,
};

const AcordoProrrogacaoHoraExtraReportView: FC<WindowComponentProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const { handleSubmit, ...formProps } = useForm<AcordoProrrogacaoHoraExtraRV>({ defaultValues: DEFAULT_VALUES, mode: 'onBlur' });

  useEffect(
    () => {
      setTimeout(() => {
        const { globalParameter } = globalReducer;
        if (globalParameter && globalParameter.empresaMFP && globalParameter.empresaMFP.referencia) {
          formProps.setValue('dataInicio', formatDate(parseRefToDate(globalParameter.empresaMFP.referencia, 1)));
          formProps.setValue('dataFim', formatDate(parseLastDay(globalParameter.empresaMFP.referencia)));
        }
        formProps.setValue('dataAssinatura', formatDate(currentDate()));
      }, 200);
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const validateExtras = () => {
    const errors = [];

    if (moment(formProps.getValues().dataFim, 'DD/MM/YYYY').isSameOrBefore(moment(formProps.getValues().dataInicio, 'DD/MM/YYYY'))) {
      errors.push('A data inicial não pode ser maior ou igual a data final.');
    }
    return errors;
  };

  return (
    <WindowComponent
      title="Acordo para Prorrogação de Hora Extra"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[
        () => hasEmpresa(globalReducer.globalParameter),
        () => hasEmpresaMFP(globalReducer.globalParameter),
        () => hasGrupoEmpresaConfig(globalReducer.globalParameter),
      ]}
      wikiId={3294}
      wikiTitle="Relatório - Acordo para Prorrogação de Hora Extra"
    >
      <ReportForm
        {...props}
        handleSubmit={handleSubmit}
        formProps={formProps}
        localStorageKey={CLASS_KEY}
        routeProps={props}
        reportBasicValues={DEFAULT_VALUES}
        reportViewOptions={DEFAULT_OPTIONS}
        extraConfigOpened={false}
        validateExtras={validateExtras}
      >
        <Alert color="danger" className="text-center">
          ATENÇÃO!
          <br />
          Para correta geração deste relatório, é necesário que o cálculo de Hora-Extra tenha sido devidamente efetuado na referência atual, e
          informar o evento previamente vinculado ao trabalhador.
        </Alert>
        <Row>
          <Col>
            <CustomizacaoTermoIS
              formProps={{ ...formProps, validation: { required: true } }}
              label="Termo"
              placeholder="Selecione o Termo"
              id="termo"
              isPrependLabel={true}
              inputSearchProps={{ size: 'xl' }}
              apenasProrrogacaoHoraExtra={true}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="dataInicio"
              label="Data Inicial da Prorrogação"
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              type={InputType.DATE}
            />
          </Col>
          <Col>
            <InputGroupInline
              id="dataFim"
              label="Data Final da Prorrogação"
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              type={InputType.DATE}
            />
          </Col>
        </Row>
        <VinculoIS
          id="vinculosSelecionados"
          formProps={{ ...formProps, validation: { required: true } }}
          label="Trabalhador(es)"
          isPrependLabel={true}
          placeholder="Selecione o(s) Trabalhador(es)"
          tooltipProps={{ message: 'Um Trabalhador deve ser selecionado' }}
          inputSearchProps={{ size: 'xl', selectProps: { isMulti: true } }}
        />
        <Row>
          <Col>
            <EventoIS
              id="evento"
              formProps={{ ...formProps, validation: { required: true } }}
              label="Evento"
              isPrependLabel={true}
              placeholder="Selecione o evento"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="dataAssinatura"
              label="Data de assinatura"
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              type={InputType.DATE}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <CustomInput type="switch" id="imprimirDuasVias" name="imprimirDuasVias" label="Imprimir duas vias" innerRef={formProps.register} />
          </Col>
        </Row>
        <br />
      </ReportForm>
    </WindowComponent>
  );
};

export default AcordoProrrogacaoHoraExtraReportView;
